        textColor: "#2CBAE7",
        descriptionBackgroundColor: "#80D2EC",
        descriptor: "University + Hiring System",
        context: "Politecnico di Milano",
        type: "Group Project",
        featuredImage: "2.2.jpg",
        description: "Alta Scuola Politecnica is an excellence Master program, run by Politecnico di Milano and Politecnico di Torino. It enrolls their best students and assigns them multisciplinary projects with a budget, to gain a Double Degree in Business Innovation. After a whole year sponsored by IBM Italy and working with Career Service (Politecnico di Milano), Bridge was born. Bridge is an assessment service that changes the way in which Alta Scuola Politecnica’s excellent students and their first jobs. By offering tailored HR workshops to uncover fresh talents, hiring managers from market leading companies are invited to evaluate ASPers’ soft skills and explain their jargon to them. My role was supporting the service design phase & branding.",
