        description: "By attending a few different hackathons and intense courses, I learnt the importance of fast prototyping -and the fun it usually implies. Starting from simple post-its, paper, acting, and going up to clay modelling, improvised tools and simulations. This skill allowed me not only to test assumpions, but also to uncover hidden needs. The playful aesthetic of a lo-fi protype allows users to feel more at ease in an uncofortable situation of “testing” and its unfinished look lets critiques and advices flow.",
        additionalContent: []
