        type: "Communication Skill",
        featuredImage: "4.2.jpg",
        description: "During the Helsinki Design Week 2015 I had the chance to hold a workshop with the aim of gathering stories and thoughts about a specific historic site of the city. Needing qualitative data to build the following project on, and having to involve shy Finns, my group decided to use the emoji language to let passers-by tell their story. We used elements from the standard iOS Emoji Keyboard, choosing a broad range not to influence the users. This way of visual storytelling is a successful internet trend, which makes it really common and intuitive for our visitors. Its international value and wide variety, made emojis a perfect co-design tool.",
        additionalContent: []
