        featuredImage: "4.3.jpg",
        description: "I had the chance to develop my thesis for a Finnish company. Its topic was research on the current industrial branding situation, which then turned into a structured design management intervention. At the end of my work, I created a handbook for the brand team: this would allow them to reproduce my analysis in time and to keep aesthetics consistently under control. The handbook is made by four frameworks, representing the phases needed for a full problem framing: from identifying the stakeholders, to semi-structured interviews, from affinity mapping, to causal relationships. This allows someone to solve the real problem, not a consequence.",
